import React from "react";
import Header from "./Header";
import Order from "./Order";
import Inventory from "./Inventory";
import sampleFishes from "../sample-fishes";
import Fish from "./Fish";
import base from "../base";

class App extends React.Component {

    state = {
        fishes: {},
        order: {}
    };

    componentDidMount(){
        
        const { params } = this.props.match;
        //1. first reinstate our local storage
        const localStorageRef = localStorage.getItem(params.storeId);
        if (localStorageRef){
            this.setState(JSON.parse(localStorageRef));
        }
        this.ref = base.syncState(`${params.storeId}/fishes`, {
            context: this, 
            state: 'fishes'
        });

        this.ref = base.syncState(`${params.storeId}/order`, {
            context: this, 
            state: 'order'
        });
    }

    componentDidUpdate(){
        console.log(this.state.order);
        localStorage.setItem(this.props.match.params.storeId, JSON.stringify(this.state.order));
    }

    //this cleans up memory after user leaves page
    componentWillUnmount(){
        base.removeBinding(this.ref);
    }



    addFish = (fish) => {

        //to update state, first take a copy of the existing state
        //we don't want to reach directly into state and modify it
        //take the copy of existing state, modify it
        const fishes = {...this.state.fishes}; //the ... means take a copy of this.state.fishes

        //now add our new fish to that fishes variable

        fishes[`fish${Date.now()}`] = fish;

        //now set the new fishes object to state;
        this.setState({
            //"fishes:" is the piece of state we are updating and "fishes" is what we are updating that piece to
            fishes: fishes //if property and state are the same, we can also just say fishes
        });

        console.log("Adding a fish");
        //the issue is that the component holding the data for this is two levels deep, use props to pass data from AddFishForm to Inventory and then Inventory to App
        
    };

    updateFish = (key, updatedFish) => {
        //1. take a copy of the current statae
        const fishes = {...this.state.fishes};
        //2. updated that state
        fishes[key] = updatedFish;
        //3. Set that to state
        this.setState({fishes});
    }

    deleteFish = (key) => {
        //1. take a copy of state 
        const fishes = {...this.state.fishes};
        //2. remove the fish from state
        //in order for firebase to also respect the removal, we can't "delete", we have to set the value of the fish to null
        fishes[key] = null;
        //3. updated the state
        this.setState({fishes});
    }

    loadSampleFishes = () =>{
        
        this.setState(
            {
                fishes: sampleFishes
            }
        )

    };

    addToOrder = key => {
        //1. Take a copy of state
        const order = {...this.state.order}; //... takes a copy of our state
        //2. Either add to order or update the number in our order
        order[key] = order[key] + 1 || 1; //if there is already that fish in the order, add one, otherwise, 1
        //3. Call setState to update out state object 
        this.setState({ order });


    }

    removeFromOrder = key =>{
        //1. Take a copy of state
        const order = {...this.state.order}; //... takes a copy of our state
        //2. Remove from order
        order[key] = null; 
        //3. Call setState to update out state object 
        this.setState({ order });
    }

    render(){
        return(
            <div className="catch-of-the-day">
                <div className="menu">
                    <Header tagline="Fresh SeaFood Market"/>
                    <ul className="fishes">
                        
                        {Object.keys(this.state.fishes).map(key => <Fish 
                                                                        details={this.state.fishes[key]} 
                                                                        key={key} 
                                                                        addToOrder = {this.addToOrder}
                                                                        index={key}
                                                                        >{key}
                                                                    </Fish>
                                                                    )//"key" is not accesible from fish, so we need to pass anothe prop called something other than key that has the key value}                        
                        }
                    </ul>
                </div>
                <Order fishes={this.state.fishes} order={this.state.order} removeFromOrder={this.removeFromOrder}></Order>
                <Inventory 
                    addFish={this.addFish} 
                    updateFish={this.updateFish}
                    deleteFish={this.deleteFish} 
                    loadSampleFishes={this.loadSampleFishes}  
                    fishes={this.state.fishes} 
                    storeId = {this.props.match.params.storeId}
                    />
                {/*<Order />*/}
            </div>
        )
    }

}

export default App;


