import React from 'react';
import { format } from 'path';
import PropTypes from "prop-types";
import {getFunName} from "../helpers";
//we need to import react into each component

class StorePicker extends React.Component{

    static propTypes = {
        history: PropTypes.object
    }

    myInput = React.createRef();

    

    goToStore = (event) => {
        //1. Stop form from submitting
        event.preventDefault();
        
        //if we need to access "this" of a component, we need to use the = (event) => syntax for the function that "this" is being referenced in
        //2. Get text from that input
        //Golden rule in React is "Don't Touch the Dom", so don't select the element and get value
        //instead sync the data into state using a ref
        const storeName = this.myInput.value.value;
        //3. Change page to /store/whatever they entered
        this.props.history.push(`/store/${storeName}`);


    }


    render(){
        return (
        <React.Fragment>    
            
            {/* This is a comment and it must be inside of <React.Fragment> or any other tag*/}
            <form className="store-selector" onSubmit={this.goToStore}>
                <h2>Please Enter a Store</h2>
                    <input type-="text" 
                        required 
                        placeholder="Store Name" 
                        defaultValue={getFunName()} 
                        ref={this.myInput} 
                      
                    />{/*in the goToStore method */}
                <button type="submit" >Visit Store</button>
                {/* Notice that the handClick function does not use () here */}
            </form>
        </React.Fragment>
        
    )
    }

}

export default StorePicker;


