import React from 'react';
import PropTypes from "prop-types";

class EditFishForm extends React.Component{

    //updating the values of thse fields is a complex case
    //we are setting the values of the fields to state, so when we change the values of the fields, state exists in two place...bad!
    //the solution is to use the handleChange() method to update the state whenever the input changes and then set the value of the fields to the updated staet
static propTypes = {
    updateFish: PropTypes.func, 
    index: PropTypes.string
}


deleteFish = event =>{
    this.props.deleteFish(this.props.index);
}
handleChange = event =>{
    //1. update that fish
    //take a copy of the current fish 
    
    const updatedFish = {
        ...this.props.fish,
        [event.currentTarget.name]: event.currentTarget.value
    };
    this.props.updateFish(this.props.index, updatedFish);

    //at this point, we can check firebase, and we see it is updated there as well as in state, as well as on the page
};

    render(){
        return <div className="fish-edit">
                     <input name="name" type="text" placeholder="Name" onChange={this.handleChange} value={this.props.fish.name} />
                    <input name="price" type="text" placeholder="Price" onChange={this.handleChange} value={this.props.fish.price}/>
                    <select name="status" type="text" placeholder="Status" onChange={this.handleChange} value={this.props.fish.status} >
                        <option value="available">Fresh!</option>
                        <option value="unavailable">Sold Out!</option>
                    </select>
                    <textarea name="desc" type="text" placeholder="Description" onChange={this.handleChange} value={this.props.fish.desc} />
                    <input name="image" type="text" placeholder="Image" onChange={this.handleChange} value={this.props.fish.image} />
                    <button type="submit" onClick={this.deleteFish}>Remove Fish</button>
               </div>

    }
}

export default EditFishForm;


