import React from "react";
import PropTypes from "prop-types";
import {formatPrice} from "../helpers";


class Fish extends React.Component {

    static propTypes = {
        details: PropTypes.shape({
            image: PropTypes.string,
            name: PropTypes.string, 
            desc: PropTypes.string, 
            status: PropTypes.string, 
            price: PropTypes.number
        }),

        addToOrder: PropTypes.func
    }
    handleClick = () => {
        this.props.addToOrder(this.props.index);
    }
    render(){
        const {image, name, price, desc, status} = this.props.details;
        //we are setting is available to be a boolean where status == "available", then true, otherwise: false
        const isAvailable = status === 'available';
        let statusButtonText = "Add To Cart";
        if (isAvailable === false){
            statusButtonText = "Sold Out!";
        }
        //if status is = avaialble, then isAvailable is true as a boolean and vice versa
        return (
            <li className="menu-fish">
                <img src={image} alt={name} />
                <h3 className="fish-name>">{name}
                    <span className="price">{formatPrice(price)}</span>
                </h3>
                <p>{desc}</p>
                <button disabled={!isAvailable} onClick={this.handleClick}>{statusButtonText}</button>
            </li>

        )
    }
}

export default Fish;


