import React from "react";
import PropTypes from "prop-types";


class AddFishForm extends React.Component {
    
    nameRef = React.createRef();
    priceRef = React.createRef();
    statusRef = React.createRef();
    descRef = React.createRef();
    imageRef = React.createRef();

    static propTypes = {
        addFish: PropTypes.func
    };
    
    createFish = (event) => {
        //1. Stop form from submitting
        event.preventDefault();
        console.log("making a fish");

        
        const fish = {
            name: this.nameRef.value.value,
            price: parseFloat(this.priceRef.value.value),
            status: this.statusRef.value.value,
            desc: this.descRef.value.value,
            image: this.imageRef.value.values
        };

        this.props.addFish(fish);

        //now that we have the data and passed it to inventory to pass to App state, we want to refresh the form
        event.currentTarget.reset();

        console.log(fish);



    }

    render(){
        return (
            <form className="fish-edit" onSubmit={this.createFish}>
                <input name="name" type="text" placeholder="Name" ref={this.nameRef} />
                <input name="price" type="text" placeholder="Price" ref={this.priceRef}/>
                <select name="status" type="text" placeholder="Status" ref={this.statusRef} >
                    <option value="available">Fresh!</option>
                    <option value="unavailable">Sold Out!</option>
                </select>
                <textarea name="desc" type="text" placeholder="Description" ref={this.descRef} />
                <input name="image" type="text" placeholder="Image" ref={this.imageRef} />
                <button type="submit"> + Add Fish</button>
            </form>

        )
    }
}

export default AddFishForm;


